import store from '../store.js';
const tokenkey = 'Access-Token';
const auth = {
  state: {
    token: store.get(tokenkey)
  },
  mutations: {
    SET_TOKEN: (state, token) => {
      store.set(tokenkey, token);
      state.token = token;
    },
    REMOVE_TOKEN: (state) => {
      store.remove(tokenkey);
      state.token = '';
    }
  }
}

export default auth
