import Vue from "vue";
import Router from "vue-router";
import store from "@/store";
import Redirect from "@/layouts/redirect";
import Layout from "@/layouts/layout";

const alarm_history = () => import("@/views/alarm/alarm_history.vue");
const alarm_contact = () => import("@/views/alarm/alarm_contact.vue");
const contact_person = () => import("@/views/alarm/alarm_contact_person.vue");
const contact_group = () => import("@/views/alarm/alarm_contact_group.vue");
const alarm_rules = () => import("@/views/alarm/alarm_rules.vue");
const create_alarm_rules = () => import("@/views/alarm/create_alarm_rules.vue");
const alarm_rules_detail = () => import("@/views/alarm/alarm_rules_detail.vue");
const bms_monitor = () => import("@/views/monitor/bms_monitor.vue");
const alarm_instance_rule = () => import("@/views/alarm/alarm_instance_rule.vue");

Vue.use(Router);
const router = new Router({
  mode: "history",
  scrollBehavior(to, form, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    } else {
      return { x: 0, y: 0 };
    }
  },
  routes: [
    {
      path: "",
      component: Layout,
      children: [
        {
          path: "/",
          name: "报警服务",
          component: Redirect,
          redirect: { name: "报警历史" },
          children: [
            {
              path: "/",
              name: "报警历史",
              component: alarm_history,
              meta: { path: "/" }
            },
            {
              path: "/histroy/:name",
              name: "规则关联历史",
              hidden: true,
              component: alarm_history,
              meta: { path: "/" }
            },
            {
              path: "/rules",
              name: "报警规则",
              component: alarm_rules,
              meta: { path: "/rules" }
            },
            {
              path: "/instance_rules/:id/:name/:type",
              name: "实例关联规则",
              component: alarm_instance_rule,
              hidden: true,
              meta: { path: "/rules" }
            },
            {
              path: "/create_alarm_rules/:type",
              name: "创建报警规则",
              hidden: true,
              component: create_alarm_rules,
              meta: { path: "/rules" }
            },
            {
              path: "/update_alarm_rules/:id/:type",
              name: "修改报警规则",
              hidden: true,
              component: create_alarm_rules,
              meta: { path: "/rules" }
            },
            {
              path: "/rules_detail/:id",
              name: "报警规则详情",
              hidden: true,
              component: alarm_rules_detail,
              meta: { path: "/rules" }
            },
            {path: '/contact_person', name: '报警联系人', component: contact_person, meta: {path: '/contact_person'}},
            {path: '/contact_group', name: '报警联系组', component: contact_group, meta: {path: '/contact_group'}},
            {path: '/contact_group/:id', name: '通知报警联系组', hidden: true, component: contact_group, meta: {path: '/contact_group'}},
          ]
        },
        {
          path: "/monitor",
          name: "裸金属监控",
          component: bms_monitor,
          meta: { path: "/monitor" }
        }
      ]
    }
  ]
});

function GetToken() {
  let strUrl = window.location.href;
  let token = null;
  let index = strUrl.indexOf("token=");
  if (index > 0) {
    token = strUrl.substr(index + 6).split("&")[0];
  }

  if (token && token.indexOf("#/") > 0) {
    token = token.substr(0, token.indexOf("#/"));
  }

  return token;
}
router.beforeEach((to, from, next) => {
  let token = GetToken();
  if (token) {
    store.commit("SET_TOKEN", token);
  }
  next();
});
export default router;
