<template>
  <div class="sidebar">
      <div class="icon"><img :src="iconImg" alt="" /></div>
      <el-menu :default-active="currentView" router>
        <template v-for="(item,i) in routes">
          <template>
            <el-menu-item :index="item.path" v-if="!!item.path && !!item.name && !item.children" :key="i">
              <span slot="title">{{item.name}}</span>
            </el-menu-item>
            <template v-if="item.children">
              <template v-if="!item.name">
                <template v-for="(m, j) in item.children" >
                    <template v-if="!m.children">
                        <el-menu-item :key="j" :index="m.path"><span slot="title">{{m.name}}</span></el-menu-item>
                    </template>
                    <template v-if="m.children">
                        <el-submenu :key="j" :index="m.path">
                           <template><span slot="title">{{m.name}}</span></template>
                           <template v-for="(n, k) in m.children" v-if="!n.hidden">
                             <el-menu-item :key="k" :index="n.path">
                                <span slot="title">{{n.name}}</span>
                              </el-menu-item>
                           </template> 
                        </el-submenu>
                    </template> 
                </template>   
              </template> 
            </template>
          </template>
        </template>
      </el-menu>
  </div>
</template>
<script>
import ElMenu from '@/components/menu/src/menu';
import ElSubmenu from '@/components/menu/src/submenu';
import ElMenuItem from '@/components/menu/src/menu-item';
export default {
  components: {ElMenu, ElSubmenu, ElMenuItem},
  data() {
      return {
         iconImg: require('../assets/img/alarm_monitor.svg'),
      }
  },
  computed: {
    currentView () {
      let n = this.$route.matched.length - 1
      let m = null
      let path = null
      for (let i = n; i >= 0; i--) {
        if (!this.$route.matched[i].name) continue
        let t = this.findRouteName(this.routes, this.$route.matched[i].name)
        if (!!t && !!t.name && !!t.path && !t.children) {
          m = t
          break
        }
      }
      path = !!m && m.meta.path || this.$route.meta.path
      return path
    },
    routes () {
      return this.$router.options.routes
    }
  },
  methods: {
    findRouteName (list, name) {
      if (!list) return
      if (list.name === name) return list
      for (let i in list) {
        if (list[i].name === name) return list[i]
        let r = this.findRouteName(list[i].children, name)
        if (!!r && r.name === name) return r
      }
    } 
  }
}
</script>
<style lang="scss">
.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  background: #fff;
  border-right: 1px solid #e7e7e7;
  text-align: center;
  width: 180px;
  .icon{
    width: 100%;
    margin: 0 auto;
    padding: 40px 0;
    img{
      width: 48px;
      height: 48px;
      display: block;
      margin: 0 auto;
    }
  }
  .el-menu {
    .el-menu-item {
      &.is-active {
         background: #E6EDFB;
      }
      span {
        color: #666;
      }
      &:hover{
          background: #E6EDFB;
          span{
            color: #455CC6;
          }
      }
    }
    .el-submenu {
      &:hover{
          background: #E6EDFB;
          span{
            color: #455CC6;
          }
      }
      .el-menu {
        background: #f2f2f2;
        .el-menu-item {
           span {
             color: #666;
           }
           min-width: auto;
           &:hover {
             background: #E6EDFB;
             span {
               color: #455CC6;
             }
           }
        }
      }
    }
  }
}
</style>
